.pages_popup {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;

  &__choice_employee {
    background-image: url("../images/leave.svg");
    width: 40px;
    height: 40px;
    background-size: 28px;
    background-position: center;
    background-repeat: no-repeat;
    border: 0;
    transform: rotate(45deg);
    background-color: transparent;
    cursor: pointer;
    border: 2px solid transparent;
    border-radius: 50%;
    padding: 5px;
    position: relative;

    &:hover {
      border: 2px solid rgb(223, 223, 223);
    }
  }

  &__button {
    width: 45%;
    display: flex;
    justify-content: space-between;
  }

  &__description_title {
    font-weight: bold;
    font-size: 14px;
    margin-top: 15px;
  }

  &__input {
    margin: 0 !important;
  }

  &__task_info {
    border-radius: 5px !important;
    width: 80%;
    height: 70%;
    padding: 30px;
  }

  &__title_task {
    font-size: 30px;
    width: 85%;
    overflow-wrap: break-word;
  }

  &__img {
    width: 200px;
    height: 200px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
    align-items: center;

    input {
      border: 1px solid lightgray;
      padding: 10px 15px;
      margin: 10px 20px;
      outline: 0;
      width: 400px;
    }
  }

  &__buttons {
    padding-top: 10px;
    padding-bottom: 15px;
    margin: 0 auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    button {
      width: 80%;
      padding: 10px 27px;
      margin-left: 20px;
      margin-right: 20px;
      border: none;
      font-weight: 500;
      font-size: 15px;
      line-height: 20px;
      color: #67a5eb;
      cursor: pointer;
      border-radius: 12px;
      // border: 4px solid transparent;
      // transition: border 0.2s;

      &:hover {
        background: #67a5eb;
        color: #fff;
      }
    }
    &_left {
      &:hover {
        background-color: rgb(238, 84, 84) !important;
        color: white !important;
      }
    }
    &_right {
      &:hover {
        background-color: rgb(103, 184, 102) !important;
        color: white !important;
        // border: 4px solid red;
      }
    }
  }

  &__container {
    background-color: #fff;
    border-radius: 20px;
  }

  &__budget_container {
    width: 50%;
    min-height: 50%;
  }

  &__center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &__title {
    margin: 28px;
    position: relative;
    display: flex;
    justify-content: space-between;
    p {
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
    }

    &_margin {
      margin: 0px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__leave_btn {
    width: 28px;
    height: 28px;
    border: 0;
    background: inherit;
    background: center no-repeat url("../images/leave.svg");
    cursor: pointer;
  }

  &__subtitle {
    p {
      font-size: 20px;
    }
  }

  .decorate {
    height: 1px;
    width: 89%;
    background-color: rgba(0, 0, 0, 0.12);
    margin: 0 auto;
  }

  &__list {
    padding: 12px 12px 20px;
    max-height: 400px;
    overflow-y: scroll;
  }

  &__item {
    width: 488px;
    padding: 10px 16px;
    height: 66px; //
    overflow: hidden;
    border-radius: 12px;
    cursor: pointer;
    position: relative;

    &_active {
      background: #67a5eb;
      p {
        color: white;
      }
      height: auto;
      min-height: 42px;
    }

    @media (max-width: 550px) {
      width: auto;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 22px;
  }

  &__web {
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    /* identical to box height, or 123% */

    letter-spacing: -0.08px;
    margin-top: 4px;
  }

  &__icon {
    display: block;
    position: absolute;
    right: 20%;
    top: 10%;
    // transform: translateY(-100%);
    width: 24px;
    height: 24px;
  }
}
